header {
    padding-bottom: 0px;

    /* background-repeat:no-repeat; */
    background-image: url("../images/flags copy.jpg");
    background-size:cover;
    background-color: beige;
    margin-bottom: 0;
}

nav {
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    padding-left: 20px;
    width: 100%;
    height: 64px;
    background-color: rgba(0, 74, 223, 0.797);
    border-bottom: black 1px;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

li {
    float: left;
    font-size: 25px;
}

li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

/* Change the link color to #111 (black) on hover */
li a:hover {
    background-color: #001F5B;
    color: white !important;
}


